<script>
import { mapState } from "vuex"
import { mapMutations } from "vuex"
import dappAbi from "../common/abi/dapp.json"
import cakeAbi from "../common/abi/cake.json"
import bnbAbi from "../common/abi/bnb.json"
export default {
    data(){
        return{
            networkVersion: 42,
            show: false,
            bnbBalance: 0,
            cakeBalance: 0,
            dappBalance: 0,
            rewards: 0,
            dappAddress:'0x231826F24293c76dfd3d0a127dB027B6f56f513b',  //项目智能合约地址
            cakeAddress:'0xBACE1b27a22f84a6BBed1eBf5B100EDD400fb485',  //cake代币合约
            // bnbAddress:'0x0d8A1149eB8d5c8dfA4E4BFdD587c2c5F66336A0',   
            loading: false,
            loading1: false,
            loading2: false,
            ceoAddress:'',
            type:'cake',
            power:'',
            directNumber: 0,
            quantity: '',
            status:'',
            investmentAmount: 0,
            buyCap: 0,
        }
    },
    computed:{
        i18n(){
            return this.$t("message");
        },
        ...mapState({
            account: (state) => state.account,
        }),
    },
     watch:{
     account(){
        if(this.type=='cake') this.getBanlance_cake()
        if(this.type=='bnb') this.getBanlance_bnb()
     }
   },
    created(){
        this.start()
    //  this.type = this.$storage.has("coinType")?this.$storage.get("coinType"):"cake"
        if(this.account){
            setTimeout(()=>{
                if(this.type=='cake') this.getBanlance_cake()
                if(this.type=='bnb') this.getBanlance_bnb()
            },200) 
        }

       

    },
    methods:{
        ...mapMutations(["login","loginout"]),
        getBanlance_cake(){
            let web3 = window.web3 = new this.$Web3(window.ethereum)
            let dappContract = new web3.eth.Contract(dappAbi,this.dappAddress)
            console.log(dappContract)
            // 合约余额
            dappContract.methods.getBalance().call().then(res=>{
                this.dappBalance = res/1e18
            })

            // 查询用户已经入金了多少
            dappContract.methods.investmentAmount(this.account).call().then(res=>{
                console.log(res)
                this.investmentAmount = Number(res/1e18)
            })

            // 查当前个人最大合计投资额
            dappContract.methods.buyCap().call().then(res=>{
                console.log(res)
                this.buyCap = Number(res/1e18)
            })
            
            // 我的蔬菜
            dappContract.methods.getMyEggs().call({from:this.account}).then(res=>{
                this.cakeBalance = res
                if(this.cakeBalance==0) return
                // 我的奖励
                dappContract.methods.calculateEggSell('0x'+(this.cakeBalance).toString(16).replace(/\.\w+$/,'')).call({from:this.account}).then(res=>{
                    this.rewards = res/1e18
                    // console.log(this.rewards,"this.rewards")
                    console.log(this.cakeBalance,"this.cakeBalance")
                })
            })

            // 推荐人地址
            dappContract.methods.ceoAddress().call({from:this.account}).then(res=>{
                this.ceoAddress = res
                //   console.log(this.ceoAddress,"this.ceoAddress")
            })

            // 算力
            dappContract.methods.getMyMiners().call({from:this.account}).then(res=>{
                this.power = res
                //   console.log(this.power,"this.power")
            })

            // 直推人数
            dappContract.methods.directNumber().call({from:this.account}).then(res=>{
                this.directNumber = res
                //   console.log(this.directNumber,"this.directNumber")
            })
            
            // cake余额
            let cakeContract = new web3.eth.Contract(cakeAbi,this.cakeAddress)
            cakeContract.methods.balanceOf(this.account).call().then(res=>{
                this.bnbBalance = res/1e18
            //   console.log(this.bnbBalance,"this.bnbBalance")
            })

            cakeContract.methods.allowance(this.account,this.dappAddress).call().then(res=>{
                this.status = res
                console.log(this.status)
            })
        },
       // 种植蔬菜
      plantFun(){
        this.loading = true
        console.log(this.status)
        if(this.status==0){
            // 授权
            let cakeContract = new web3.eth.Contract(cakeAbi,this.cakeAddress)
            cakeContract.methods.approve(this.dappAddress,"0x" + Number(100000000000000000000000000).toString(16)).send({from:this.account}).then(res=>{
                console.log(res)
                this.loading = false
                this.getBanlance_cake()
            }).catch(err=>{
                this.loading = false
            })
        }else{
            if(!this.quantity||this.quantity==0)  {
                this.$tools.warning(this.i18n.a20)
                this.loading = false
                return
            }
            if(Number(this.quantity) > Number(this.bnbBalance)) {
                this.$tools.warning(this.i18n.a21)
                this.loading = false
                return 
            }
            if(this.quantity>this.buyCap - this.investmentAmount) {
                this.$tools.warning(`最大购买数量${this.buyCap - this.investmentAmount}CAKE`)
                this.loading = false
                return
            }
            // 种植蔬菜
            let dappContract = this.type=='cake'? new web3.eth.Contract(dappAbi,this.dappAddress) : new web3.eth.Contract(bnbAbi,this.bnbAddress)
            let link = window.location.href
            let arr = link.split("=")
            switch(arr.length){
                case 1:
                var ceoAddress = this.ceoAddress
                break;
                case 2:
                if(arr[1].includes('tokenpocket')){ 
                    var ceoAddress = this.ceoAddress
                }else{  
                    let str = arr[1]
                    if(str.includes("#")){
                        var array = str.split("#")
                        var ceoAddress = array[0]
                    }else{
                        var ceoAddress = str
                    }
                }
                break;
                case 3:
                    let str = arr[2]
                    if(str.includes("#")){
                        var array = str.split("#")
                        var ceoAddress = array[0]
                    }else{
                        var ceoAddress = str
                    }
                break;
            }

            console.log(ceoAddress)
            dappContract.methods.buyEggs(
                '0x'+(this.quantity*1e18).toString(16).replace(/\.\w+$/,''),
                ceoAddress
            ).send({from:this.account}).then(result=>{
                this.quantity = ''
                this.loading = false
                this.$tools.success(this.i18n.a23)
                this.getBanlance_cake()
            }).catch(err=>{
                if(err.code==4001){
                    this.$tools.warning(this.i18n.tip)
                }else{
                    this.$tools.warning(err.message)
                }
                this.loading = false
                console.log(err)
            })
        }
      },

      // 重新成长
      regrowFun(){
        if(this.loading1) return
        this.loading1 = true
        let dappContract = this.type=='cake'? new web3.eth.Contract(dappAbi,this.dappAddress) : new web3.eth.Contract(bnbAbi,this.bnbAddress)
        dappContract.methods.hatchEggs().send({from:this.account}).then(res=>{
          this.loading1 = false
          this.$tools.success(this.i18n.a24)
          this.getBanlance_cake()
        }).catch(err=>{
          if(err.code==4001){
            this.$tools.warning(this.i18n.tip)
          }else{
            this.$tools.warning(err.message)
          }
          this.loading1 = false
        })   
      },

      // 收成
      collectFun(){
        if(this.loading2) return
        this.loading2 = true
        let dappContract = this.type =='cake'? new web3.eth.Contract(dappAbi,this.dappAddress) : new web3.eth.Contract(bnbAbi,this.bnbAddress)
        dappContract.methods.sellEggs().send({from:this.account}).then(res=>{
          this.loading2 = false
          this.$tools.success(this.i18n.a25)
          this.getBanlance_cake()
        }).catch(err=>{
          if(err.code==4001){
            this.$tools.warning(this.i18n.tip)
          }else{
            this.$tools.warning(err.message)
          }
          this.loading2 = false
        })
      },

        addressFilter(str) {
            if (!str) return "--";
            const str1 = str.substring(0, 5);
            const str2 = str.substring(str.length - 5, str.length);
            return str1 + "..." + str2;
        },
        addressFilter1(str) {
            if (!str) return "--";
            const str1 = str.substring(0, 9);
            const str2 = str.substring(str.length - 9, str.length);
            return str1 + "..." + str2;
        },
        // 检测钱包环境
        async connectWallet(type){
            const { ethereum } = await window;
            if(!(ethereum && ethereum.isMetaMask)){
            this.$storage.remove("account")
            return this.$tools.warning(this.i18n.a26)
            }else if( ethereum.networkVersion != this.networkVersion ){
            this.$tools.warning("您需要在连接前切换到Kovan链")
            }else{
            console.log("用户钱包环境正常")
            this.start()
            this.connect(type);
            }
        },
        // PHONE链接钱包
        async connect(type){
            switch(type){
                case 1:
                    ethereum.request({
                        method: 'wallet_requestPermissions',
                        params: [{ eth_accounts: {} }],
                    }).then( async(permissions) => {
                    if( permissions[0].parentCapability=="eth_accounts" ){
                        const accounts = await ethereum.request({ method: 'eth_requestAccounts' });
                        this.login(accounts[0])
                        this.$tools.success("连接成功")
                    }
                    })
                break;
                case 2:
                    const accounts = await ethereum.request({ method: 'eth_requestAccounts' });
                    this.login(accounts[0])
                    this.$tools.success(this.i18n.a28)
                break; 
            } 
        },
        // 监控账户
        start() {
            this.web3 = window.web3 = new this.$Web3(window.ethereum);
            window.ethereum.autoRefreshOnNetworkChange = false;
            // 监控切换地址
            window.ethereum.on("accountsChanged", (item) => {
                console.log(item[0])
                this.login(item[0])
                console.log('accountsChanged', item[0])
            });
            // 监控切换网络
                window.ethereum.on("networkChanged", () => {
                this.loginout()
                console.log('networkChanged')
            });
        },
        loginOut(){
            this.show = false
            this.account = ""
            this.loginout()
        }
    }
}
</script>

